  flex-direction: row;
}
.weather-forecast .item .weather-icon {
  display: block;
  font-size: 3em;
  width: 90px;
  text-align: right;
}
.weather-forecast .item .data {
  margin-left: 1em;
}

.weather-map {
  width: 100%;
  height: 400px;
  border: 1px solid black;
}

main {
  min-height: 500px;
}
main.has-sidebar-right {
  display: grid;
  grid-template-columns: auto 250px;
  grid-gap: 1em;
}
main.has-sidebar-left {
  display: grid;
  grid-template-columns: 250px auto;
  grid-gap: 1em;
}
main.has-sidebar-left.has-sidebar-right {
  grid-template-columns: 250px auto 250px;
}
@media screen and (max-width: 720px) {
