.region-sidebar {
  position: sticky;
  top: 1em;
}
.region-sidebar .wrapper {
  position: sticky;
  top: 1em;
}
.region-sidebar h4 {
  background: #37474f;
  color: #fff;
  padding: 0.2em 1em;
}

.breadcrumb-list ul {
  display: flex;
}
.breadcrumb-list li {
  display: inline;
}
.breadcrumb-list li::after {
  content: ">";
  margin: 0 0.5em;
}

.next-previous {
  display: flex;
}
.next-previous .previous {
  flex: 1;
  text-align: left;
  order: 1;
}
.next-previous .next {
