/* Message */
.message {
  display: flex;
  flex-direction: row;
  margin: 1em 2em;
  border-radius: 0.3rem;
  box-shadow: 0 3px 6px 1px rgba(0, 0, 0, 0.5);
}
.message::before {
  display: flex;
  justify-content: center;
  align-items: center;
  background: #fff;
  font-size: 2rem;
  padding: 0.5em;
  width: 1.5em;
  border-radius: 0.3rem 0 0 0.3rem;
}
.message.ok {
  color: #94d82d;
  background: #f5fbea;
}
.message.ok::before {
  content: "";
  transform: rotate(0.5turn);
}
.message.warn {
  color: #ff922b;
  background: #fff4ea;
}
.message.warn::before {
  content: "";
}
.message.err {
  color: #be2320;
  background: #ffeaea;
}
.message.err::before {
  content: "";
}
.message .content {
  padding: 1rem;
  color: rgba(0, 0, 0, 0.7);
  font-family: Helvetica, Verdana, Arial, sans-serif;
  font-size: 1.1em;
  font-weight: bold;
}